








































import { Component, Vue } from "vue-property-decorator";
import {
  TripRouteForm,
  TicketTypeForm,
  PersonalInformationForm,
  ContactCaptureForm,
  DocumentationForm
} from "@/components/forms";
import PotentialClaimAmount from "@/components/PotentialClaimAmount.vue";
import { namespace, Getter } from "vuex-class";
import { CaseData } from "@/interfaces";
import { mutationTypes, getterTypes, actionTypes } from "@/store";

const newCaseStore = namespace("newCase");

@Component({
  components: {
    TripRouteForm,
    TicketTypeForm,
    PersonalInformationForm,
    PotentialClaimAmount,
    ContactCaptureForm,
    DocumentationForm
  }
})
export default class Home extends Vue {
  dateModalOpen = false;
  tripDate = null;
  maxDate = new Date().toISOString().substr(0, 10);

  @newCaseStore.State caseData!: CaseData;

  @newCaseStore.Mutation(mutationTypes.NEW_CASE_DATA) updateCaseData!: (
    caseData: CaseData
  ) => void;

  @newCaseStore.Action(actionTypes.SUBMIT_NEW_USER_CASE) submitCase!: (params: {
    firstName: string;
    lastName: string;
    email: string;
    caseData: CaseData;
  }) => Promise<any>;

  @Getter(getterTypes.OPTIONS_CITIES) cityOptions!: any[];
  @Getter(getterTypes.OPTIONS_OPERATORS) operatorOptions!: any[];

  currentStep = 1;

  created() {
    const params = this.$route.query;
    if (params && params.departedFrom && params.destination) {
      this.updateCaseData({
        ...this.caseData,
        departureCityId: parseInt(params.departedFrom as string),
        destinationCityId: parseInt(params.destination as string)
      });
    }
  }

  onNextClicked() {
    if (this.currentStep === 3) {
      this.currentStep += 1;
    } else {
      if ((this.$refs.form as any).validate()) {
        this.updateCaseData({
          ...this.caseData
        });

        if (this.currentStep === 4) {
          /* this.submitCase({ */
          /*   firstName: "Jeff", */
          /*   lastName: "Kehler", */
          /*   email: "jeffrey.kehler@gmail.com", */
          /*   caseData: this.caseData */
          /* }).then(() => { */
          /*   this.currentStep += 1; */
          /* }); */
          this.currentStep += 1;
        } else {
          this.currentStep += 1;
        }
      }
    }
  }

  onCaseDataChanged(caseData: CaseData) {
    this.updateCaseData(caseData);
  }

  get buttonText(): string {
    if (this.currentStep === 3) return "Claim Now!";
    return "Next";
  }
}
