























































import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import { CaseData } from "@/interfaces";

@Component
export default class TripRouteForm extends Vue {
  @Prop({ required: true }) caseData!: CaseData;
  @Prop({ required: true }) cityOptions!: any[]
  @Prop({ required: true }) operatorOptions!: any[]

  valid = true;
  dateModalOpen = false;
  tripDate = null;
  maxDate = new Date().toISOString().substr(0, 10);

  @Watch("tripDate")
  onTripDateChanged(value: string) {
    this.valueChanged("tripDate", value);
  }

  departedFrom: any = null
  destination: any = null

  validate() {
    return (this.$refs.form as any).validate();
  }

  valueChanged(field: string, value: any) {
    const caseData = { ...this.caseData };
    (caseData as any)[field] = value;
    this.$emit("change", caseData);
  }

  created() {
    const queryParams = this.$route.query
    if ('departedFrom' in queryParams && 'destination' in queryParams) {
      this.departedFrom = this.$route.query['departedFrom']
      this.destination = this.$route.query['destination']
    }
  }
}

