import { snakeCase, camelCase } from "change-case";

export function changeKeys(
  obj: any,
  casing: 'snake' | 'camel'
): any {
  const f = casing === 'snake' ? snakeCase : camelCase
  if (!obj || obj.constructor !== Object) {
    return obj;
  }

  return Object.keys(obj).reduce((all: any, key) => {
    const newKey = f(key);
    all[newKey] =
      obj[key] && obj[key].constructor === Object
        ? changeKeys(obj[key], casing)
        : obj[key];
    return all;
  }, {});
}
