import Vue from "vue";
import Vuex, { MutationTree, ActionTree, GetterTree } from "vuex";
import { actionTypes, mutationTypes, getterTypes } from "./types";
import newCase from "./modules/newCase";
// import axios from "axios";
import _ from "underscore";

Vue.use(Vuex);

export class RootState {
  configData: any;

  constructor() {
    this.configData = {};
  }
}

const operators = [{ id: "vy", name: "Vy" }];
const cities = [
  { id: 1, name: "Bergen", operators: ["vy"] },
  { id: 2, name: "Voss", operators: ["vy"] },
  { id: 3, name: "Myrdal", operators: ["vy"] },
  { id: 4, name: "Flåm", operators: ["vy"] },
  { id: 5, name: "Geilo", operators: ["vy"] },
  { id: 6, name: "Gol", operators: ["vy"] },
  { id: 7, name: "Hønefoss", operators: ["vy"] },
  { id: 8, name: "Tønsberg", operators: ["vy"] },
  { id: 9, name: "Kongsberg", operators: ["vy"] },
  { id: 10, name: "Notodden", operators: ["vy"] },
  { id: 11, name: "Nordagutu", operators: ["vy"] },
  { id: 12, name: "Bø", operators: ["vy"] },
  { id: 13, name: "Skien", operators: ["vy"] },
  { id: 14, name: "Larvik", operators: ["vy"] },
  { id: 15, name: "Drammen", operators: ["vy"] },
  { id: 16, name: "Asker", operators: ["vy"] },
  { id: 17, name: "Spikkestad", operators: ["vy"] },
  { id: 18, name: "Oslo", operators: ["vy"] },
  { id: 19, name: "Ski", operators: ["vy"] },
  { id: 20, name: "Mysen", operators: ["vy"] },
  { id: 21, name: "Rakkestad", operators: ["vy"] },
  { id: 22, name: "Moss", operators: ["vy"] },
  { id: 23, name: "Halden", operators: ["vy"] },
  { id: 24, name: "Göteborg", operators: ["vy"] },
  { id: 25, name: "Lillestrøm", operators: ["vy"] },
  { id: 26, name: "Kongsvinger", operators: ["vy"] },
  { id: 27, name: "Dal", operators: ["vy"] },
  { id: 28, name: "Gardermoen", operators: ["vy"] },
  { id: 29, name: "Eidsvoll", operators: ["vy"] },
  { id: 30, name: "Hamar", operators: ["vy"] },
  { id: 31, name: "Lillehammer", operators: ["vy"] },
  { id: 32, name: "Otta", operators: ["vy"] },
  { id: 33, name: "Dombås", operators: ["vy"] },
  { id: 34, name: "Gjøvik", operators: ["vy"] },
];

const mutations: MutationTree<RootState> = {
  [mutationTypes.CONFIG_DATA](state, value) {
    state.configData = value;
  },
};

const actions: ActionTree<RootState, RootState> = {
  [actionTypes.FETCH_CONFIG_DATA]({ commit }) {
    commit(mutationTypes.CONFIG_DATA, { cities, operators })
  }
  // async [actionTypes.FETCH_CONFIG_DATA]({ commit }) {
  //   return axios.get("http://localhost:5000/api/v1/config").then((resp) => {
  //     commit(mutationTypes.CONFIG_DATA, resp.data);
  //   });
  // },
};

const getters: GetterTree<RootState, RootState> = {
  [getterTypes.OPTIONS_CITIES](state) {
    if (state.configData && state.configData["cities"]) {
      return _.sortBy(
        (state.configData["cities"] as any[]).map((i) => {
          return { value: i["id"], text: i["name"] };
        }),
        "text"
      );
    }
    return [];
  },
  [getterTypes.OPTIONS_OPERATORS](state) {
    if (state.configData && state.configData["operators"]) {
      return _.sortBy(
        (state.configData["operators"] as any[]).map((i) => {
          return { value: i["id"], text: i["name"] };
        }),
        "text"
      );
    }
    return [];
  },
};

export default new Vuex.Store({
  state: new RootState(),
  mutations,
  actions,
  getters,
  modules: {
    newCase,
  },
});

export { actionTypes, mutationTypes, getterTypes };
