


































import { Component, Vue, Prop } from "vue-property-decorator";
import { CaseData } from "@/interfaces";

@Component
export default class TicketTypeForm extends Vue {
  @Prop({ required: true }) caseData!: CaseData;
  valid = true;

  delayOptions = [
    { text: "Less than 30 minutes", value: "0-30m" },
    { text: "30 minutes to 1 hour", value: "30m-1h" },
    { text: "1-2 hours", value: "1-2h" },
    { text: "3-4 hours", value: "3-4h" },
    { text: "4+ hours", value: "4+h" },
  ];

  ticketPriceRules = [
    (v: any) => !!v || "Ticket price is required",
    (v: any) =>
      /^\d{0,4}(?:[.,]\d{1,2})?$/.test(v) || "Ticket price is not valid"
  ];

  ticketType = "single";

  validate() {
    return (this.$refs.form as any).validate();
  }

  valueChanged(field: string, value: any) {
    const caseData = { ...this.caseData };
    (caseData as any)[field] = value;
    this.$emit("change", caseData);
  }
}
