










































































































import { Component, Vue } from "vue-property-decorator";
import { Getter } from "vuex-class";
import { getterTypes } from "@/store";

@Component
export default class Home extends Vue {
  cities = ["Asker", "Drammen", "Olso"];
  companies = ["Flytoget", "Go-Ahead Norge", "NSB Gjøvikbanen", "Vy"];

  @Getter(getterTypes.OPTIONS_CITIES) cityOptions!: any[];

  form = {
    departedFrom: null,
    destination: null
  };

  checkCompensation() {
    if ((this.$refs.form as any).validate()) {
      this.$router.push({
        path: "claim",
        query: {
          departedFrom: this.form.departedFrom,
          destination: this.form.destination
        }
      });
    }
  }
}
