import { MutationTree, ActionTree, GetterTree } from "vuex";
import { RootState, actionTypes, mutationTypes, getterTypes } from "@/store";
import { CaseData } from "@/interfaces";
import axios from "axios";
import { changeKeys } from "@/helpers";

class State {
  caseData?: CaseData;

  constructor() {
    this.caseData = { operatorId: "vy" };
  }
}

const mutations: MutationTree<State> = {
  [mutationTypes.NEW_CASE_DATA](state, value) {
    state.caseData = value;
  },
};

const actions: ActionTree<State, RootState> = {
  async [actionTypes.SUBMIT_NEW_USER_CASE](
    { commit },
    params: {
      firstName: string;
      lastName: string;
      email: string;
      caseData: CaseData;
    }
  ) {
    return axios
      .post(
        "http://localhost:5000/api/v1/auth/register-client",
        changeKeys(params, "snake")
      )
      .then((resp) => {
        console.log(resp);
      });
  },
};

export default {
  namespaced: true,
  state: new State(),
  mutations,
  actions,
};
